import Server from './server';

class API extends Server{
  /**
   *  
   *  @url ://XXXXXX
   *  status1
   *  @method post
   *  @return {promise}
   */
  async uploadImg(params = {}){
    try{
      let result = await this.axios('post', '://XXXXXX', params); 
      if(result && result.status === 1){
        return result;
      }else{
        let err = {
          tip: '',
          response: result,
          data: params,
          url: '://XXXXXX',
        }
        throw err;
      }
    }catch(err){
      throw err;
    }
  }

  /**
   *  
   *  @url ://XXXXXX
   *  http_code200
   *  @method get
   *  @return {promise}
   */
  async getRecord(params = {}){
    try{
      let result = await this.axios('get', `/XXX/${params}`);
      if(result){
        return result;
      }else{
        let err = {
          tip: '',
          response: result,
          data: params,
          url: '://XXX',
        }
        throw err;
      }
    }catch(err){
      throw err;
    }
  }

}

export default new API();


