import React, { Component } from 'react';
// import logo from '../../assets/logo.svg';
import htLogo from '../../assets/logo.png';
import Ycheck from '../../assets/yellowCheck.png';
import secondCode from '../../assets/twoCode.png';
// import Gcheck from '../../assets/Gheck.png';
import './home.css';
import API from '../../api/api';

class Home extends Component{
    constructor(props) {
        super(props);
        this.state = {
            phone: '',
            qq:'',
            message:'',
            suggest: [],
            suggestData:[],
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    checkChange(event){
        let {value,checked}=event.target;
    }

    handleChange(event) {
        let {name,value,checked}=event.target;
        if(name=='suggest'){
            let suggest=this.state.suggest;
            if(checked)suggest.push(value);
            else suggest=suggest.filter((d)=>d!==value);
            this.setState({suggest});
        }else{
            console.log({[name]:value});
            this.setState({[name]:value});
        }
    }
    //
    componentDidMount(){
        fetch('http://10.10.19.88:8082/survey/queryAll',)//
            .then((response) => response.json())//
            .then((responseText) => {//
                this.setState({suggestData:responseText.content});
            })
    }
    getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
    }
    handleSubmit(event) {
        if (this.state.message.length == 0 && this.state.suggest.length == 0) {
            alert('')
            return;
        } else if (this.state.suggest.length == 0) {
            alert('')
            return;
        } else if (this.state.message.length == 0) {
            alert('')
            return;
        } else if (this.state.phone != "") {
            let reg = /^1[0-9]{10}/;
            if (!reg.test(this.state.phone)) {
                alert('')
                return;
            }
        }

        // alert('');
        // try {
        //       let result = await
        //         API.getRecord({
        //         method: 'post',
        //         url: 'http://10.10.19.88:8082/survey/add',
        //         data: {
        //             phone: this.state.phone,
        //             qq: this.state.qq,
        //             message: this.state.message,
        //             suggest: this.state.suggest,
        //
        //         }
        //     });
        // }
        // catch(err){
        //         console.error(err);
        //     }
        let params ={
            phone: this.state.phone,
            qq: this.state.qq,
            description: this.state.message,
            ids: this.state.suggest.join(",")
        };
        console.log(params)
        fetch('http://10.10.19.88:8082/survey/add',{
            method:'POST',
            crossDomain: true,
            headers: {
                'Content-Type':'application/x-www-form-urlencoded'
            },
            // body:  JSON.stringify({param: JSON.stringify(params)})
            body:  'param='+JSON.stringify(params)
        }).then((response) => {
            if (response.ok) {
                return response.json();
            }
        }).then((json) => {
            alert(JSON.stringify(json));
        }).catch((error) => {
            console.error(error);
        });

    }
    render(){
    return (
      <main className="home">
       <section className="pro-content">
           <section className="pro-nav">
               <img src={htLogo} alt="1111111"/>
           </section>
           <section className="pro-center">
             <section className="pro-prompt">
                 <p><span><img src={Ycheck} alt=""/></span>e!</p>
                 <div>e</div>
             </section>
               <div className="form">
                  <div className="checkBox">
                      {this.state.suggestData.map((d,i) => (
                          <label  className="check" key={i}><input name="suggest" type="checkbox" value={d.id} onChange={this.handleChange} /> {d.name}</label>
                      ))}
                  </div>
                   <textarea id="message" name="message" value={this.state.message} onChange={this.handleChange}  placeholder="">

                   </textarea>
                   <div className="contactWay">
                       <h4><span> ()</span></h4>
                       <label className="phoneNumber"> : <input type="tel"  name="phone" value={this.state.phone} onChange={this.handleChange} /></label> <label>QQ: <input type="text" name="qq" value={this.state.qq} onChange={this.handleChange}  /></label><br />
                   </div>
                   {/*<h2 onClick={this.handleSubmit}></h2>*/}
                   <div className="formSubmit"><input onClick={this.handleSubmit}  type="submit" value="" /></div>
                   <div className="twoCode">
                       <img src={secondCode} alt=""/>
                       <p>eapp</p>
                   </div>
               </div>
           </section>
       </section>
      </main>
    )
  }
}
export default Home;








